import { useEffect, useState, useRef } from 'react';
import Speech from 'react-speech';
import { checkAnswerService } from '../../services/Questions';

function Sorting({ question, onAnswer }) {
    const [order, setOrder] = useState({ ...question, answer: [], selection: 0 });
    const options = useRef();
    const blanks = useRef();
    let click = true;

    useEffect(() => setOrder({ ...question, answer: [], selection: 0 }), [question]);

    const handleCheck = async () => {
        if (order.answered) {
            if (click) {
                onAnswer(null, null, true);
                click = false;
            }
        }
        else {
            if (click) {
                let next = await checkAnswerService({ qid: question.id, sentence: order.answer.join(' '), type: order.type });
                if (next) click = false;
                onAnswer(next, order.answer);
            }
        }
    }

    const handleDrop = (e, i) => {
        if (i < 0) {                                            // move word from answer to options
            const index = moveToOption(order.drag);
            if (e.type === 'dblclick') handleAnimation(e.target, options.current.children[index]);
            if (order.selection < 0) order.selection = order.drag;
        } else if (order.from === 'a') {                        // dragged from answer
            const word = order.answer[i];
            order.answer[i] = order.answer[order.drag];         // switch the words
            order.answer[order.drag] = word;
        } else {
            moveToAnswer(order.drag, i);                        // dragged from options
        }
        setOrder({ ...order });
    }

    const handleSelect = (e, i) => {
        if (order.selection < 0) {
            const index = moveToOption(i);
            handleAnimation(e.target, options.current.children[index]);
        }
        setOrder({ ...order, selection: i });
    }

    const handleAnimation = (from, to) => {
        const rect_from = from.getBoundingClientRect();
        const rect_to = to.getBoundingClientRect();
        to.style.setProperty('--left', rect_from.left - rect_to.left + 'px');
        to.style.setProperty('--top', rect_from.top - rect_to.top + 'px');
        to.setAttribute('data-word', from.innerText);
        to.classList.add('move-word');
        setTimeout(() => to.classList.remove('move-word'), 500);
    }

    const handleOrderClick = (e, i) => {
        handleAnimation(e.target, blanks.current.children[order.selection]);
        moveToAnswer(i, order.selection);
        setOrder({ ...order });
    }

    const moveToAnswer = (from, to) => {
        const word = order.answer[to];
        const value = order.options[from]?.value || order.options[from];
        if (word) order.options.find(from => from.value === word).disabled = false;
        order.options[from] = { value, disabled: true };
        order.answer[to] = value;
        nextSelection();
    }

    const moveToOption = (from) => {
        const word = order.answer.splice(from, 1, null)[0];
        const index = order.options.findIndex(i => {
            if (i.value === word) {
                i.disabled = false;
                return true;
            }
            return false;
        });
        return index;
    }

    const nextSelection = () => {
        const index = order.answer.findIndex(i => i == null);
        if (index < 0 && order.answer.length < order.blanks.length) order.selection = order.answer.length;
        else order.selection = index;
    }

    return (
        <>
            <div className="question-header">
                <div>
                    <h2>{order.question}</h2>
                    {order.quotes && <p className="question-quotes"><em>{order.quotes}</em></p>}
                    <p>Sort the words to form the sentence.</p>
                </div>
                {order.img && <img src={order.img} alt={order.question} />}
            </div>
            <div className="order-question" ref={blanks}>
                {order.blanks.map((item, itemI) =>
                    <span key={'a' + itemI} className="el-blank" draggable={order.answer[itemI] ? true : undefined}
                        onDragStart={() => { order.drag = itemI; order.from = 'a'; }} onDragOver={e => e.preventDefault()} onDrop={e => handleDrop(e, itemI)}
                        onClick={e => handleSelect(e, itemI)} onDoubleClick={order.answer[itemI] ? e => { order.drag = itemI; handleDrop(e, -1); } : undefined}
                        style={{ backgroundColor: order.selection === itemI && '#80000033', textTransform: itemI === 0 && 'capitalize' }}>
                        {order.answered ? order.answered[itemI] : order.answer[itemI] || item}
                    </span>
                )}
            </div>
            <div className="order-question" ref={options} onDragOver={e => { if (order.from === 'a') e.preventDefault() }} onDrop={e => handleDrop(e, -1)}>
                {order.options.map((item, itemI) =>
                    <button key={'o' + itemI} className="el-btn" onClick={e => handleOrderClick(e, itemI)}
                        onDragStart={() => { order.drag = itemI; order.from = 'o' }} draggable disabled={item.disabled}>
                        {item.value || item}
                    </button>
                )}
            </div>
            <div className="el-option" style={!order.answered && order.selection > -1 ? { visibility: 'hidden' } : undefined} onClick={handleCheck}>
                <Speech textAsButton styles={CheckButton} displayText={order.answered ? 'Next' : 'Check'}
                    voice="Google UK English Female" text={order[order.answered ? 'answered' : 'answer'].join(' ')} />
            </div>
        </>
    );
}
export default Sorting;

const CheckButton = {
    play: {
        hover: { backgroundColor: '#f8f8ff' },
        button: {
            backgroundColor: '#12dc5533',
            borderColor: '#12dc55'
        }
    },
    resume: { button: {} },
    pause: { button: {} },
    stop: { button: {} }
};