import { useEffect } from 'react';
import Speech from 'react-speech';
import { getRandomColor } from '../../helper/functionality';
import { checkAnswerService } from '../../services/Questions';

function Choosing({ question, onAnswer }) {
    let click = true;

    useEffect(() => {
        const rsPlay = document.getElementsByClassName('rs-play');
        for (const element of rsPlay) {
            const color = getRandomColor();
            const style = `background-color:${color}33;border-color:${color}`;
            element.addEventListener('mouseleave', () => { element.style = style; });
            element.style = style;
        }
    }, [question]);

    const handleCheck = async (aid) => {
        if (question.answered) {
            if (click) {
                onAnswer(null, null, true);
                click = false;
            }
        }
        else {
            if (click) {
                let next = await checkAnswerService({ qid: question.id, aid, type: question.type, variant: question.variant });
                if (next) { click = false; }
                onAnswer(next, aid);
            }
        }
    }

    return (
        <>
            <div className="question-header">
                <div>
                    <h2>{question.question}</h2>
                    {question.quotes && <p className="question-quotes"><em>{question.quotes}</em></p>}
                    <p><span className="question-type">{question.type}</span> Question: Choose correct {question.variant}.</p>
                </div>
                {question.img && <img src={question.img} alt={question.question} />}
            </div>
            {question.options.map((item, itemI) =>
                <div key={itemI} className="el-option" onClick={() => handleCheck(item.id)}>
                    {!question.answered || item.id === question.answered ?
                        <Speech textAsButton displayText={item.value} voice="Google UK English Female" text={item.value} /> :
                        <button className="rs-play" disabled>{item.value}</button>
                    }
                </div>
            )}
        </>
    );
}
export default Choosing;