export const SIGNUP_CONFIRMED = '[signup action] confirmed signup';
export const SIGNUP_FAILED = '[signup action] failed signup';
export const LOGIN_CONFIRMED = '[login action] confirmed login';
export const LOGIN_FAILED = '[login action] failed login';
export const LOADING_TOGGLE = '[Loading action] toggle loading';
export const LOGOUT = '[Logout action] logout action';

const initialState = {
    showLoading: false,
    successMessage: '',
    errorMessage: '',
    auth: ''
};

export function AuthReducer(state = initialState, action) {
    switch (action.type) {
        case SIGNUP_CONFIRMED:
            return {
                ...state,
                auth: action.payload,
                errorMessage: '',
                successMessage: 'Signup Successfully Completed',
                showLoading: false
            };
        case LOGIN_CONFIRMED:
            return {
                ...state,
                auth: action.payload,
                errorMessage: '',
                successMessage: 'Login Successfully Completed',
                showLoading: false
            };
        case LOGOUT:
            return {
                ...state,
                errorMessage: '',
                successMessage: '',
                auth: ''
            };
        case SIGNUP_FAILED:
        case LOGIN_FAILED:
            return {
                ...state,
                errorMessage: action.payload,
                successMessage: '',
                showLoading: false
            };
        case LOADING_TOGGLE:
            return { ...state, showLoading: action.payload };
        default:
            return state;
    }
}