import { useEffect, useRef, useState } from 'react';
import Speech from 'react-speech';
import { getRandomColor } from '../../helper/functionality';
import { checkAnswerService } from '../../services/Questions';

function Matching({ question, onAnswer }) {
    const [selected, setSelected] = useState([]);
    const count = useRef(0);

    useEffect(() => {
        count.current = 0;
        const rsPlay = document.getElementsByClassName('rs-play');
        for (const element of rsPlay) {
            const color = getRandomColor();
            const style = `background-color:${color}33;border-color:${color}`;
            element.addEventListener('mouseenter', () => { element.style = 'background-color:#f8f8ff'; });
            element.addEventListener('mouseleave', () => { element.style = style; });
            element.style = style;
        }
    }, [question]);

    const handleCheck = async () => onAnswer(await checkAnswerService({ type: 'matching' }));

    const handleSelect = (i) => {
        let index = selected.indexOf(i);
        if (index > -1 && selected.length < 2) {
            selected.splice(index, 1);
            setSelected([...selected]);
        } else {
            if (selected.length < 2) {
                selected.push(i);
                setSelected([...selected]);
                if (selected.length === 2) {
                    if (question.qList[selected[0]].en === question.qList[selected[1]].en) {
                        question.qList[selected[0]].correct = 0;
                        question.qList[selected[1]].correct = 0;
                        setTimeout(() => {
                            question.qList[selected[0]].correct = 1;
                            question.qList[selected[1]].correct = 1;
                            count.current += 2;
                            if (count.current >= question.qList.length)
                                handleCheck();
                            setSelected([]);
                        }, 500);
                    } else {
                        onAnswer(false);
                        question.qList[selected[0]].wrong = true;
                        question.qList[selected[1]].wrong = true;
                        setTimeout(() => {
                            question.qList[selected[0]].wrong = false;
                            question.qList[selected[1]].wrong = false;
                            setSelected([]);
                        }, 500);
                    }
                }
            }
        }
    }

    return <>
        <h2>Choose Pairs</h2>
        <p>Match the words with their meanings.</p>
        <div className="matching-question">
            {question.qList.map((item, itemI) =>
                item.pair ? item.img ?
                    <div key={itemI} className={`el-option${selected.includes(itemI) ? ' selected' : ''}${item.wrong ? ' false' : ''}${item.correct === 0 ? ' true' : ''}`}>
                        <button className="rs-play" disabled={item.correct === 1} onClick={() => handleSelect(itemI)}><img src={item.img} alt={item.img.tr} /></button>
                    </div> :
                    <div key={itemI} className={`el-option${selected.includes(itemI) ? ' selected' : ''}${item.wrong ? ' false' : ''}${item.correct === 0 ? ' true' : ''}`}>
                        <button className="rs-play" disabled={item.correct === 1} onClick={() => handleSelect(itemI)}>{item.tr}</button>
                    </div> :
                    <div key={itemI} className={`el-option${selected.includes(itemI) ? ' selected' : ''}${item.wrong ? ' false' : ''}${item.correct === 0 ? ' true' : ''}`}
                        onClick={item.disabled ? undefined : () => handleSelect(itemI)}>
                        {item.correct === 1 ? <button className="rs-play" disabled>{item.en}</button> :
                            <Speech key={itemI} textAsButton displayText={item.en} voice="Google UK English Female" text={item.en} />
                        }
                    </div>
            )}
        </div>
    </>
}
export default Matching;